<template>
    <a-form @submit="handleSubmit" layout="inline" :autoFormCreate="(form)=>{this.form = form}">
        <template v-if="form">
            <a-form-item
                    :validateStatus="userNameError() ? 'error' : ''"
                    :help="userNameError() || ''"
                    fieldDecoratorId="user_login"
                    :fieldDecoratorOptions="{rules: [{ required: true, message: '请输入账号!' }]}"
            >
                <a-input placeholder='请输入账号' size="large">
                    <a-icon slot="prefix" type='user' style="color:rgba(0,0,0,.25)"/>
                </a-input>
            </a-form-item>
            <a-form-item
                    :validateStatus="passwordError() ? 'error' : ''"
                    :help="passwordError() || ''"
                    fieldDecoratorId="user_password"
                    :fieldDecoratorOptions="{rules: [{ required: true, message: '请输入密码!' }]}"
            >
                <a-input type='password' placeholder='请输入密码' size="large">
                    <a-icon slot="prefix" type='lock' style="color:rgba(0,0,0,.25)"/>
                </a-input>
            </a-form-item>
            <div style="padding-top: 10px">
                <a-form-item>
                    <a-button
                            type='primary'
                            htmlType='submit'
                            size="large"
                            :disabled="hasErrors(form.getFieldsError())"
                    >
                        党员登录
                    </a-button>
                </a-form-item>
                <a-form-item>
                    <a-button
                            type='primary'
                            size="large"
                            @click="jump"
                    >
                        群众登录
                    </a-button>
                </a-form-item>
            </div>
        </template>
    </a-form>
</template>

<script>
    import {login} from '@/api/user'

    function hasErrors(fieldsError) {
        return Object.keys(fieldsError).some(field => fieldsError[field])
    }

    export default {
        data() {
            return {
                hasErrors,
                form: null,
            }
        },
        mounted() {
            this.$nextTick(() => {
                this.form.validateFields()
            })
        },
        methods: {
            // Only show error after a field is touched.
            userNameError() {
                const {getFieldError, isFieldTouched} = this.form
                return isFieldTouched('userName') && getFieldError('userName')
            },
            // Only show error after a field is touched.
            passwordError() {
                const {getFieldError, isFieldTouched} = this.form
                return isFieldTouched('password') && getFieldError('password')
            },
            handleSubmit(e) {
                e.preventDefault()
                this.form.validateFields((err, values) => {
                    if (!err) {
                        login(values).then(res => {
                            let user = res.data.user;
                            let type = user.type_format;
                            switch (type) {
                                case '超级管理员':
                                    this.$router.push({name: 'super'})
                                    break;
                                default:
                                    this.$router.push({name: 'pc首页'})
                                    break;
                            }
                        })
                    }
                })
            },
            jump(){
                this.$router.push({name: '群众首页'});
            }
        },
    }
</script>
